import { Component, OnInit } from '@angular/core';
import { CadastroService } from '../../shared/services/cadastro/cadastro';
import { LogarService } from '../../shared/services/login/logar';
import { Router } from '@angular/router';
import { AtivarComponents } from '../../shared/services/ativarComponents';
import { NotificationsService } from 'angular2-notifications';

declare var FB: any;
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  cadastroSeg: boolean;
  nome: string;
  email: string;
  senha: string;

  // tslint:disable-next-line:max-line-length
  constructor(private cadastroService: CadastroService,
    private logarService: LogarService,
    private router: Router,
    public showComponente: AtivarComponents,
    private notif: NotificationsService) {
  }


  sucess(msg) {
    this.notif.success(
      'Sucesso',
      msg,
      {
        timeOut: 3000,
        showProgressBar: true,
        pauseOnHover: false,
        clickToClose: true,
        maxLength: 50
      }
    )
  }

  erro(msg) {
    this.notif.error(
      'Erro',
      msg,
      {
        timeOut: 3000,
        showProgressBar: true,
        pauseOnHover: false,
        clickToClose: true,
        maxLength: 50
      }
    )
  }

  ngOnInit() {
  }

  cadastrarSegments() {
    this.cadastroSeg = true;
  }

  logarSegments() {
    this.cadastroSeg = false;
  }
  cadastrar() {
    if (this.email && this.senha && this.nome) {
      const obj = {
        email: this.email,
        senha: this.senha,
        nome: this.nome
      };
      this.cadastroService.cadastrar(obj).then(data => {
        console.log(data);
        const response = data.resultado;
        if (response === 'sucesso') {
          this.sucess('Cadastrado com sucesso!');
          this.cadastroSeg = false;
        } else if (response === 'erro: email existente') {
          this.erro('Email j existente!');
        }
      });
    } else {
      this.erro('Preencha todos os campos');
    }
  
  }

  logar() {
    const obj = {
      email: this.email,
      senha: this.senha
    };
    this.logarService.logar(obj).then(data => {
      console.log(data);
      const response = data.resultado;
      if (response === 'sucesso') {
        const tam = data.retorno.length;
        if (tam > 0) {
          this.showComponente.show = true;
          localStorage.setItem('email', data.retorno[0].email);
          localStorage.setItem('nome', data.retorno[0].nome);
          this.router.navigate(['bolsonaro']);
        } else {
          this.erro('Erro: verifique seu email ou senha');
        }
      } else {
        this.erro('Erro: verifique seu email ou senha');
      }
    });
  }

  public onLoginClick() {
    // this.router.navigate(['./home']);
    FB.getLoginStatus((response) => {
      console.log(response);
      if (response.status === 'connected') {
        this.router.navigate(['./bolsonaro']);
      }
      else {
        FB.login((loginResponse) => {
          this.router.navigate(['./bolsonaro']);
        });
      }
    });
  }
}
